<template>
  <v-container>
    <div class="alert-div">
      <v-alert
        max-width="277px"
        class="alert"
        :value="alert"
        dense
        type="success"
        >Item added to cart!</v-alert
      >
    </div>
    <cart :cart="cart"></cart>
    <router-view :products="products" @addToCart="addToCart"></router-view>
  </v-container>
</template>

<script>
import Cart from '@/components/petshop/Cart';

export default {
  components: { Cart },
  data() {
    return {
      products: this.$store.state.petshop.products,
      cart: [],
      alert: false,
    };
  },

  methods: {
    addToCart(product) {
      this.cart.push(product);
      this.alert = true;
      setTimeout(() => {
        this.alert = false;
      }, 2000);
    },
  },
};
</script>

<style lang="scss" scoped>
.alert-div {
  display: flex;
  justify-content: center;
}
.alert {
  position: fixed;
  top: 80px;
  z-index: 2;
}
</style>
